@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Recursive:wght@300;400&display=swap');

* {
  margin: 0;
  padding: 0;
}

/* pageSpace margins are 40%, so whatever margins are 40% they represent the pageSpace */

/* desktop view */
@media only screen and (min-width: 768px) {
  body {
    font-family: 'Recursive', 'Poppins', sans-serif;
    background-color:rgb(51, 50, 50);
  }

  h1 {
    font-size: 6vmin;
    padding-bottom: 1%
  }

  h2{
    font-size: 4vmin
  }

  h3 {
    color: rgb(218, 216, 216);
    font-size: 2.27vmin
  }

  ::selection {
    background:  rgb(98, 133, 98); 
  }

  ::-moz-selection {
    background:  rgb(98, 133, 98);
  }

  .nav{
    display: block;
    text-align: right;
    line-height: 8vmin;
    width: fit-content;
  }

  .about-section {
    color: rgb(218, 216, 216);
    max-width: 900px;
    width: 55%;
    min-width: 55%;
    padding-left: 20px;
    position: relative;
    line-height: 6vmin;
  }

  .wrapper {
    width: 40%;
    margin: 0 auto;
    position: relative;
  }

  .fixed-wrapper {
    width: 40%;
    position: absolute;
    top: -5vmin;
    left: 90%;
  }

  .fixed {
    width: 30%;
    position: fixed;
    /* Do not set top / left! */
  }

  /* change this to better represent the different sections on each page */
  .pageSpace{
    margin-top:90vmin
  }

  .mainprofile{
    border-radius:50%;
    width:41%
  }

  .container {
    max-width: 95%;
    margin: 30px auto;
    overflow: auto;
    min-height: 500px;
  }

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0px;
  }

  .name {
    color:rgb(218, 216, 216);
  }

  .beforeclick {
    color:rgb(83, 84, 170);
    cursor: pointer;
    transition: all ease-in-out 150ms;
  }

  .beforeclick:hover {
    color: rgb(132, 146, 132);
    transition: all ease-in-out 225ms;
  }

  .afterclick {
    color: rgb(98, 133, 98);
    cursor: pointer;
    transition: all ease-in-out 150ms;
  }

  /* for mobile, make this the same color as selected. gets obfuscated */
  .afterclick:hover {
    color: rgb(78, 78, 78);
    opacity: 80%;
    transition: all ease-in-out 225ms;
  }

  .selected {
    color:rgb(218, 216, 216);
    cursor: pointer;
    transition: all ease-in-out 150ms;
  }

  .selected:hover {
    color: rgb(218, 216, 216);
    opacity: 80%;
    transition: all ease-in-out 225ms;
  }

  .blurb{
    width: 40%;
    color:rgb(218, 216, 216);
    text-align: center;
    margin: 70px 0 0 60px;
    line-height: 7vmin;
    padding: 8px;
  }

  .btn {
    display: flex;
    background:  rgb(98, 133, 98);
    color: rgb(218, 216, 216);
    border: none;
    padding: 1vmin 2vmin;
    margin-top: 11vmin;
    margin-left: 2.1%;
    border-radius: 0px;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.1vw;
    font-family: inherit;
    transition: all ease 100ms;
    transform: scale(1.6);
  }

  .btn:focus {
    outline: none
  }

  .btn:active {
    transform: scale(1.5);
    transition: all ease 100ms;
  }

  .contact-form {
    border: none;
    width: 85%;
    margin: 90vmin 20px 10% 20px;
    color: rgb(218, 216, 216);
    padding:20px;
    align-items: center;
  }

  input{
    width: 70%;
    height: 6vmin;
    margin: 10px 0px 25px 0px;
    background-color: rgb(218, 216, 216);
    border: none;
    font-size:4vmin;
    padding:7px;
    font-family: 'Poppins', sans-serif;
  }

  textarea{
    width: 70%;
    height: 20vmin;
    margin: 10px 0px -50px 0px;
    background-color: rgb(218, 216, 216);
    border: none;
    font-size:4vmin;
    padding: 7px;
    font-family: 'Poppins', sans-serif;
  }

  .github {
    transition: all ease-in-out 150ms;
    margin-right:15px;
    min-height: 13%;
  }

  .linkedin {
    transition: all ease-in-out 150ms;
    margin-left:15px;
    opacity:75%;
    min-height: 30%;
  }

  /* change scale?? */
  .linkedin:hover, .github:hover {
    transition: all ease-in-out 225ms;
    opacity: 15%;
  }

  footer {
    position: fixed;
    bottom: 0;
    padding-bottom: 2vmin;
    display: inline-flex;
    left:47%;
    right:53%
  }

}

